    text-align: -webkit-right;
    overflow: hidden;
    display: block;
    position: unset;
    background-color: rgb(0, 0, 0);
    width: 100%;
    z-index: 999;
    min-height: 55px;
  }

  .sticky {
    position: fixed !important;
    top: 0;
  }

  /*---------------------------------hamburger icon--------------------------------------------------------*/
  .hamburger-menu {
    position: unset !important;
    right: 0;
    left: 0;
    bottom: unset;
    float: right;
    position: absolute;
    top: 0;
    margin: 5px 15px 5px 15px;
  }
  #hamburger-icon {
    float: right;
  }

  /*--------------------------------------menu---------------------------------------------------*/

  .nav-menu {
    text-align: center;
    position: relative;
    right: 0%;
    width: 100%;
    background-color: rgb(0, 0, 0);
    margin-top: 60px;
  }

  /*
    -----------------------------------------------------------------------------------------
  } */

  #hamburger-icon {
    width: 50px;
    height: 40px;
    margin: 0;
  }

  #hamburger-icon span {
    height: 5px;
  }

  #hamburger-icon span:nth-child(1) {
    top: 0px;
  }

  #hamburger-icon span:nth-child(2),
  #hamburger-icon span:nth-child(3) {
    top: 14px;
  }

  #hamburger-icon span:nth-child(4) {
    top: 28px;
  }
}

