.progress {
    border-radius: 1rem;
    height: "25px";
    width: 100% !important;
    /* background-color: #e0e0e0; */
    background-color: white;
    height: 0.6rem;
  }


.progress-bar {
    background-color: #5aa183 !important;
  }
  .animation-progress {
    margin-top: 80px;
    margin-bottom: 100px;
  }
  
  .animation-progress-bar {
    width: 100%;
    -webkit-animation: load 5s linear infinite;
            animation: load 5s linear infinite;
  }
  @-webkit-keyframes load {
    0% {
      width: 0%;
    }
  
    70% {
      width: 100%;
    }
    100% {
      width: 100%;
    }
  }
  @keyframes load {
    0% {
      width: 0%;
    }
  
    70% {
      width: 100%;
    }
    100% {
      width: 100%;
    }
  }
  
  .progress-text {
    font-family: "Muli", sans-serif;
    font-size: 22px;
    text-align: left;
    padding-left: 20px;
  }
  
